import React, { useState } from "react";
import { Button, Col, Form, Row } from "@themesberg/react-bootstrap";
import { useGetCaptcha } from "../../../hooks/ims/useCaptcha";
import { useLogin } from "../../../hooks/ims/useLogin";
import toast from "react-hot-toast";
import bglogin from "../../../assets/img/register_bg_2.png";
import { Link, useLocation } from "react-router-dom";
import { LoadingCircle } from "../../components/icons";
import { useHistory } from "react-router-dom";
import { inputFields, newRegisterFormData } from "../../../constants";
import { useVerification } from "../../../hooks/ims/useVerification";

export default function IMSLogin() {
  const { data: captchaUrl, isLoading: captchaLoading } = useGetCaptcha();
  const { mutateAsync, isLoading } = useLogin();
  const { mutateAsync: getRegistrationLink, isLoading: isLoadingVerification } =
    useVerification();
  const [formData, setFormData] = useState(newRegisterFormData);

  const history = useHistory();
  const { search } = useLocation();
  const searchParams = new URLSearchParams(search);
  const token = searchParams.get("token");

  const [college_email, setCollege_email] = useState("");
  const [roll_no, setRoll_no] = useState("");

  const handleInputChange = (e) => {
    const { name, value } = e.target;

    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleVerification = async (e) => {
    e.preventDefault();
    if (college_email === "" || roll_no === "" || isLoadingVerification) {
      return;
    }
    const data = await getRegistrationLink({ email: college_email, roll_no: roll_no });
    if (data.success) {
      toast.success(
        "Email sent successfully. Please check your email for registration link"
      );
      history.push("/email-verified?success=true");
    } else {
      toast.error("Email verification failed. Please try again.");
      history.push("/email-verified?success=false");
    }
  };

  async function handleSubmit(e) {
    e.preventDefault();
    if (captchaUrl === undefined || isLoading) {
      return;
    }

    const data = await mutateAsync({ ...formData, token });

    if (data.success) {
      toast.success("Registration successful. Please wait for admin approval.");
    }
  }
  return (
    <div style={{ background: "#262b40" }}>
      <div
        className="d-flex flex-column justify-content-center gap-2 align-items-center"
        style={{
          backgroundImage: `url(${bglogin})`,
          height: "100vh",
          width: "100vw",
          backgroundSize: "cover"
        }}
      >
        {token ? (
          token.length === 64 ? (
            <>
              <div
                className="mx-auto flex-grow-1 p-2"
                style={{ overflow: "scroll", maxHeight: "90vh", width: "100%" }}
              >
                <div
                  className="pt-4 mx-auto"
                  style={{ width: "400px", maxWidth: "100%", color: "orange" }}
                >
                  <h2 style={{ color: "darkorange" }}>Register</h2>
                  <Form onSubmit={handleSubmit} className="mt-3">
                    {inputFields.map((input, index) => {
                      if (
                        input.type === "text" ||
                        input.type === "number" ||
                        input.type === "password"
                      ) {
                        return (
                          <Row
                            className="mt-3"
                            key={input.label + index + input.type}
                          >
                            <Col
                              xs={12}
                              // className="mt-3 registerInput"
                            >
                              <Form.Group>
                                <Form.Label>{input.label}</Form.Label>
                                <Form.Control
                                  type={input.type}
                                  required
                                  name={input.name}
                                  value={formData[input.name]}
                                  onChange={handleInputChange}
                                  {...input}
                                />
                                {input.message && (
                                  <p className="mt-2">{input.message}</p>
                                )}
                              </Form.Group>
                            </Col>
                          </Row>
                        );
                      } else if (input.type === "select") {
                        return (
                          <Form.Group
                            className="mt-3"
                            key={input.label + index + input.type}
                          >
                            <Form.Label>{input.label}</Form.Label>
                            <Form.Select
                              name={input.name}
                              onChange={handleInputChange}
                              required
                            >
                              {input.options.map((option, index) => (
                                <option
                                  key={option.name + index}
                                  value={option.value}
                                >
                                  {option.name}
                                </option>
                              ))}
                            </Form.Select>
                          </Form.Group>
                        );
                      } else if (input.type === "captcha") {
                        return (
                          <Row
                            className="mt-3"
                            key={input.label + index + input.type}
                          >
                            <Col xs={12}>
                              <Form.Group>
                                <Form.Label>Captcha</Form.Label>
                                <Row className="d-flex align-items-center justify-content-between">
                                  <Col xs={8}>
                                    <Form.Control
                                      type={input.type}
                                      required
                                      name={input.name}
                                      value={formData[input.name]}
                                      onChange={handleInputChange}
                                      {...input}
                                    />
                                  </Col>
                                  <Col className="d-flex align-items-center justify-content-center">
                                    {captchaLoading ? (
                                      <LoadingCircle />
                                    ) : (
                                      <img
                                        src={captchaUrl}
                                        alt="captha"
                                        style={{ width: "100%" }}
                                      />
                                    )}
                                  </Col>
                                </Row>
                              </Form.Group>
                            </Col>
                          </Row>
                        );
                      } else return null;
                    })}
                    <Row className="mt-5">
                      <Col xs={12}>
                        <Button
                          style={{
                            width: "100%",
                            background:
                              "linear-gradient(to right, #fb7b4a, #ee3344)"
                          }}
                          disabled={isLoading}
                          type="submit"
                        >
                          {isLoading ? <LoadingCircle /> : "Register"}
                        </Button>
                      </Col>
                    </Row>
                  </Form>
                  <div className="text-center">or</div>
                  <div
                    className="mt-2 p-2 text-white text-center"
                    style={{
                      width: "100%",
                      borderRadius: "8px",
                      background: "linear-gradient(to right, #fb7b4a, #ee3344)"
                    }}
                  >
                    <Link
                      to={"/login"}
                      className="text-white text-center"
                      style={{
                        width: "100%",
                        display: "block"
                      }}
                    >
                      Back to Login
                    </Link>
                  </div>
                </div>
              </div>
            </>
          ) : (
            <div style={{ width: "400px", maxWidth: "100%", color: "orange" }}>
              <h2 style={{ color: "darkorange" }}>Email Verification</h2>
              <p>Seems you are lost, this is not a valid registration link.</p>
            </div>
          )
        ) : (
          <div
            className="pt-4 mx-auto"
            style={{ width: "400px", maxWidth: "100%", color: "orange" }}
          >
            <Form onSubmit={handleVerification} className="mt-3">
              <Row className="mt-3">
                <Col xs={12}>
                  <Form.Group>
                    <Form.Label>Roll no</Form.Label>
                    <Form.Control
                      type="text"
                      required
                      name="roll_no"
                      value={roll_no}
                      onChange={(e) => setRoll_no(e.target.value.toUpperCase())}
                    />
                  </Form.Group>
                </Col>
              </Row>
              <Row className="mt-3">
                <Col xs={12}>
                  <Form.Group>
                    <Form.Label>College Email</Form.Label>
                    <Form.Control
                      type="email"
                      required
                      name="college_email"
                      value={college_email}
                      onChange={(e) => setCollege_email(e.target.value)}
                    />
                    <p>
                      A verification link will be sent to this email for
                      verification. You can't change it later so fill carefully.
                      The email must be college email address, otherwise the
                      verification will fail.
                    </p>
                  </Form.Group>
                </Col>
              </Row>
              <Row className="mt-2">
                <Col xs={12}>
                  <Button
                    style={{
                      width: "100%",
                      background: "linear-gradient(to right, #fb7b4a, #ee3344)"
                    }}
                    disabled={isLoading}
                    type="submit"
                  >
                    {isLoading ? <LoadingCircle /> : "Register"}
                  </Button>
                </Col>
              </Row>
            </Form>
          </div>
        )}
      </div>
    </div>
  );
}
